<style lang="scss" scoped>

</style>
<template>
  <!-- <page-box> -->
        <div class="ui-list ui-box" slot="list-box">
            <div class="box-header ui-list-header">
                <div>我的部门</div>
                <div>
                    <el-button type="primary" size="mini" @click="back">返回</el-button>
                </div>
            </div>
            <el-table :data="dataList"  :cell-style="cellStyle" :header-cell-style="rowClass">
                <el-table-column prop="userId" label="用户id"></el-table-column>
                <el-table-column prop="username" label="用户名"></el-table-column>
                <el-table-column prop="email" label="邮箱"></el-table-column>
                <el-table-column prop="phone" label="手机号"></el-table-column>
                <el-table-column prop="sex" label="性别">
                    <template slot-scope="scope">
                        <span v-if="scope.row.sex==1">男</span>
                        <span v-else>女</span>
                    </template>
                </el-table-column>               
                <el-table-column prop="status" label="状态">
                    <template slot-scope="scope">
                        <el-button type="success" icon="el-icon-check" circle size="mini" v-if="scope.row.status==1"></el-button>
                        <el-button type="danger" icon="el-icon-close" circle v-else size="mini"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
  <!-- </page-box> -->
</template>
<script>
import pageBox from '@/components/pageBox'
export default {
    name: 'myDepartment',
    components:{
        pageBox,
    },
    data(){
        return{
            dataList: [],
        }
    },
    created(){
        this.getData()
    },
    methods:{
        getData(){
          let myDepartmentId=''
            get(`/pms_api/user/detail`).then(res => {
                myDepartmentId=res.data.hasDepart[0].departmentId
                post('/pms_api/department/assign/department/list/',{'departmentId':myDepartmentId}).then(res => {
                    this.dataList=res.rows
                }).catch(err => {
                    console.log(err)
                })
            }).catch(err => {
                console.log(err)
            })
        },
        back(){
          console.log(this.$route.query.title);
          this.$router.back(-1)
        },
        //表格居中
        cellStyle({row, colum, rowIndex, columIndex}){
            return "text-align: center";
        },
        rowClass({row, rowIndex}){
            return "text-align: center";
        }
    }
}
</script>





